        <li>发帖日期</li>
        <li>回帖数</li>
        <li>详情</li>
        <li>删除</li>
      </ul>
      <ul class="bar" v-for="n in 10">
        <li>1</li>
        <li>3</li>
        <li>小小123</li>
        <li>大家好</li>
        <li>希望在农场...</li>
        <li>发帖日期</li>
        <li>回帖数</li>
        <li><a href="javascript:;" @click="postShow">详情</a></li>
        <li><a href="javascript:;">删除</a></li>
      </ul>
